import Vue from 'vue';
import Component from 'vue-class-component';
import { Action } from 'vuex-class';

import TextField from 'muse-ui/textField';
import flatButton from 'muse-ui/flatButton';

import { IPersonalParams } from '~/entity/route_params';
// import Utils from '~/utils';
import { ToastMsg } from '~/entity/toast';
import Api from '~/api';
import { Watch } from 'vue-property-decorator';

import ShareImage from '~/components/share_image/index.vue';

interface ISearchFriend {
    birthday: string;
    headPortrait: string;
    realname: string;
    tel: string;
    token: string;
    uid: string;
    loveState: number;
    sex: number;
    status: number;
}

@Component({
    components: {
        'mu-text-field': TextField,
        'mu-flat-button': flatButton,
        'share-image': ShareImage
    }
})
export default class SearchAddFriends extends Vue {

    showShareImage = false;

    inputNum: string = '';
    searchResult: ISearchFriend[] = [];

    @Action('showToast')
    showToast;
    @Action('showLoading')
    showLoading;
    @Action('hideLoading')
    hideLoading;

    $refs: {
        searchInput: HTMLElement;
    };

    @Watch('inputNum')
    checkNum(newVal, oldVal) {
        console.log(`new: ${newVal}, old: ${oldVal}`);
        let inputNumTemp = newVal.replace(/[^0-9]/g, '');
        this.inputNum = inputNumTemp;
        if (oldVal !== inputNumTemp) {            
            this.searchResult = [];
        }    
    }

    activated() {
        this.inputNum = '';
        this.showShareImage = false;
        this.searchResult = [];
    }

    async searchFriends() {
        this.showLoading();
        try {
            const friendRes = await Api.searchFriendById(this.inputNum);
            this.hideLoading();
            const result = friendRes.body.obj;
            this.searchResult = [];
            if (result !== null) {
                this.searchResult.push(result);
            } else {
                this.showToast(new ToastMsg('搜索不到相关好友，请检查下输入的手机号是否正确'));
            }           
            console.log(this.searchResult);            
        } catch (e) {
            this.hideLoading();
            this.showToast(new ToastMsg(e.message));
        }
    } 
    
    clearNum() {
        this.inputNum = '';
    }

    // isAlreadyFriend(uid: string) {
    //     return Utils.isMyFriends(uid);
    // }
    toPersonalPage(uid: string) {
        const params: IPersonalParams = {
            action: 'otherUser',
            id: uid
        };
        this['$router'].push({
            name: 'personalPage',
            params
        }); 
    }

    async addFriend(fid) {
        this.showLoading();
        try {
            await Api.addFriendById(fid);
            this.hideLoading();
            this.showToast(new ToastMsg('已申请添加对方好友，等待答复'));
        } catch (e) {
            this.hideLoading();
            this.showToast(new ToastMsg(e.message));
        }
    }
}



